namespace $ {
	export class $hyoo_crus_realm_book extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \ Realm
		 * ```
		 */
		menu_title() {
			return " Realm"
		}
		
		/**
		 * ```tree
		 * param \land
		 * ```
		 */
		param() {
			return "land"
		}
		
		/**
		 * ```tree
		 * realm $hyoo_crus_realm
		 * ```
		 */
		@ $mol_mem
		realm() {
			const obj = new this.$.$hyoo_crus_realm()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Spread* <= Land*
		 * ```
		 */
		Spread(id: any) {
			return this.Land(id)
		}
		
		/**
		 * ```tree
		 * menu_foot / <= Land_new
		 * ```
		 */
		menu_foot() {
			return [
				this.Land_new()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * land* $hyoo_crus_land
		 * ```
		 */
		@ $mol_mem_key
		land(id: any) {
			const obj = new this.$.$hyoo_crus_land()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Land* $hyoo_crus_land_page
		 * 	land <= land*
		 * 	tools / <= Spread_close
		 * ```
		 */
		@ $mol_mem_key
		Land(id: any) {
			const obj = new this.$.$hyoo_crus_land_page()
			
			obj.land = () => this.land(id)
			obj.tools = () => [
				this.Spread_close()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Land_new_icon $mol_icon_plus
		 * ```
		 */
		@ $mol_mem
		Land_new_icon() {
			const obj = new this.$.$mol_icon_plus()
			
			return obj
		}
		
		/**
		 * ```tree
		 * land_new? null
		 * ```
		 */
		@ $mol_mem
		land_new(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Land_new $mol_button_minor
		 * 	hint \Grab new Land
		 * 	sub / <= Land_new_icon
		 * 	click? <=> land_new?
		 * ```
		 */
		@ $mol_mem
		Land_new() {
			const obj = new this.$.$mol_button_minor()
			
			obj.hint = () => "Grab new Land"
			obj.sub = () => [
				this.Land_new_icon()
			] as readonly any[]
			obj.click = (next?: any) => this.land_new(next)
			
			return obj
		}
	}
	
}

