namespace $ {
	export class $hyoo_crus_land_page extends $mol_page {
		
		/**
		 * ```tree
		 * land $hyoo_crus_land
		 * ```
		 */
		@ $mol_mem
		land() {
			const obj = new this.$.$hyoo_crus_land()
			
			return obj
		}
		
		/**
		 * ```tree
		 * tools /
		 * ```
		 */
		tools() {
			return [
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * body / <= Node*AAAAAAAA
		 * ```
		 */
		body() {
			return [
				this.Node("AAAAAAAA")
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * foot / <= Encrypted
		 * ```
		 */
		foot() {
			return [
				this.Encrypted()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * node_title* \
		 * ```
		 */
		node_title(id: any) {
			return ""
		}
		
		/**
		 * ```tree
		 * node* $hyoo_crus_node
		 * ```
		 */
		@ $mol_mem_key
		node(id: any) {
			const obj = new this.$.$hyoo_crus_node()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Node*AAAAAAAA $hyoo_crus_node_dump
		 * 	title <= node_title*
		 * 	node <= node*
		 * ```
		 */
		@ $mol_mem_key
		Node(id: any) {
			const obj = new this.$.$hyoo_crus_node_dump()
			
			obj.title = () => this.node_title(id)
			obj.node = () => this.node(id)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Encrypted_icon $mol_icon_lock
		 * ```
		 */
		@ $mol_mem
		Encrypted_icon() {
			const obj = new this.$.$mol_icon_lock()
			
			return obj
		}
		
		/**
		 * ```tree
		 * encrypted? false
		 * ```
		 */
		@ $mol_mem
		encrypted(next?: any) {
			if ( next !== undefined ) return next as never
			return false
		}
		
		/**
		 * ```tree
		 * encryptable true
		 * ```
		 */
		encryptable() {
			return true
		}
		
		/**
		 * ```tree
		 * Encrypted $mol_check_icon
		 * 	hint \Encrypt
		 * 	Icon <= Encrypted_icon
		 * 	checked? <=> encrypted?
		 * 	enabled <= encryptable
		 * ```
		 */
		@ $mol_mem
		Encrypted() {
			const obj = new this.$.$mol_check_icon()
			
			obj.hint = () => "Encrypt"
			obj.Icon = () => this.Encrypted_icon()
			obj.checked = (next?: any) => this.encrypted(next)
			obj.enabled = () => this.encryptable()
			
			return obj
		}
	}
	
}

