namespace $ {
	export class $hyoo_crus_app extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \CRUSDB
		 * ```
		 */
		menu_title() {
			return "CRUSDB"
		}
		
		/**
		 * ```tree
		 * param \section
		 * ```
		 */
		param() {
			return "section"
		}
		
		/**
		 * ```tree
		 * realm $hyoo_crus_realm
		 * ```
		 */
		@ $mol_mem
		realm() {
			const obj = new this.$.$hyoo_crus_realm()
			
			return obj
		}
		
		/**
		 * ```tree
		 * menu_foot / <= Source
		 * ```
		 */
		menu_foot() {
			return [
				this.Source()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * spreads *
		 * 	\
		 * 		<= Intro
		 * 	realm <= Realm
		 * 	casting <= Casting
		 * ```
		 */
		spreads() {
			return {
				"": this.Intro(),
				realm: this.Realm(),
				casting: this.Casting()
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * Placeholder null
		 * ```
		 */
		Placeholder() {
			return null as any
		}
		
		/**
		 * ```tree
		 * Source $mol_link_source uri \https://github.com/hyoo-ru/cras.hyoo.ru
		 * ```
		 */
		@ $mol_mem
		Source() {
			const obj = new this.$.$mol_link_source()
			
			obj.uri = () => "https://github.com/hyoo-ru/cras.hyoo.ru"
			
			return obj
		}
		
		/**
		 * ```tree
		 * intro \
		 * ```
		 */
		intro() {
			return ""
		}
		
		/**
		 * ```tree
		 * Intro_content $mol_text text <= intro
		 * ```
		 */
		@ $mol_mem
		Intro_content() {
			const obj = new this.$.$mol_text()
			
			obj.text = () => this.intro()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Intro $mol_page
		 * 	title \ Intro
		 * 	body / <= Intro_content
		 * ```
		 */
		@ $mol_mem
		Intro() {
			const obj = new this.$.$mol_page()
			
			obj.title = () => " Intro"
			obj.body = () => [
				this.Intro_content()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Realm $hyoo_crus_realm_book
		 * 	realm <= realm
		 * 	addon_tools / <= Spread_close
		 * ```
		 */
		@ $mol_mem
		Realm() {
			const obj = new this.$.$hyoo_crus_realm_book()
			
			obj.realm = () => this.realm()
			obj.addon_tools = () => [
				this.Spread_close()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Casting $hyoo_calc
		 * 	title \ Casting
		 * 	editable false
		 * 	sheet_id \jfls98_bf0zru
		 * 	Fork null
		 * 	New null
		 * 	Lights null
		 * 	Source null
		 * 	Current null
		 * 	tools / <= Spread_close
		 * ```
		 */
		@ $mol_mem
		Casting() {
			const obj = new this.$.$hyoo_calc()
			
			obj.title = () => " Casting"
			obj.editable = () => false
			obj.sheet_id = () => "jfls98_bf0zru"
			obj.Fork = () => null as any
			obj.New = () => null as any
			obj.Lights = () => null as any
			obj.Source = () => null as any
			obj.Current = () => null as any
			obj.tools = () => [
				this.Spread_close()
			] as readonly any[]
			
			return obj
		}
	}
	
}

